import React from 'react'
import Button from '@mui/material/Button'
import './DetailsButton.css'
import { alpha, styled } from '@mui/material/styles';
import { useMediaQuery } from 'react-responsive';
import { Typography } from '@mui/material';
import { useEffect } from 'react'
import {Link} from 'react-scroll'
import {Snackbar} from '@mui/material';
import {Alert} from '@mui/material';

const DetailsButton = (props) => {
   const [open, setOpen] = React.useState(false);

    const DetailsButton = styled(Button)(({ theme }) => ({
           color: 'white',
           backgroundColor: '#F57854',
          '&:hover': {         
            backgroundColor : '#FF825E'
          },
      }));

      const isDesktop = useMediaQuery({
        query: "(min-device-width: 1200px)",
    });

    const handleClick = () => {
        console.log("asds")
        setOpen(true);  
    }

    const handleClose = () => {
      setOpen(false);
    }

    return(
      isDesktop?
        <>
             <DetailsButton onClick={handleClick} variant='contained' style = {{borderRadius:'1vw',padding:'5px' ,direction : 'rtl', width:'100%',}}>
              <Link activeClass='active' to='contact-page' spy = {true} smooth = {true} duration={1000}>
              <Typography style = {{fontSize: '1.5vw', fontFamily : 'Segoe UI'}}>{props.text}</Typography>
              </Link>
            </DetailsButton>
            <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
              <Alert severity="success" sx={{ width: '100%'}}>
                 המייל נשלח בהצלחה, ניצור אתכם קשר בהקדם!
              </Alert>
            </Snackbar> 
        </>
   
        :
        <>
          <DetailsButton  variant='contained' style = {{borderRadius:'1vw',padding:'2vw'}}>
            <Link activeClass='active' to='contact-page' spy = {true} smooth = {true} duration={1000}>
            <Typography style = {{fontSize: '2vh', fontFamily : 'Segoe UI'}}>כן! אשמח לקבל פרטים נוספים</Typography>
            </Link>
          </DetailsButton>
        </>
       
    )
}

export default DetailsButton