import React from 'react'
import Hero from '../hero/hero'
import Grid from '@mui/material/Grid'
import './proccesspage.css'
import ProccessStep from './proccessStep/proccessStep'
import CreateIcon from '@mui/icons-material/Create';
import QuestionAnswerIcon from '@mui/icons-material/QuestionAnswer';
import CameraAltIcon from '@mui/icons-material/CameraAlt';
import SettingsIcon from '@mui/icons-material/Settings';
import ManageSearchIcon from '@mui/icons-material/ManageSearch';
import AutoFixNormalIcon from '@mui/icons-material/AutoFixNormal';
import AutoStoriesIcon from '@mui/icons-material/AutoStories';
import CelebrationIcon from '@mui/icons-material/Celebration';
import DetailsButton from '../DetailsButton/DetailsButton'
import { useMediaQuery } from 'react-responsive';
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/all'
import { useEffect , useRef} from 'react'

const ProccessPage = (props) => {
    const stepVars = [[CreateIcon,"משאירים פרטים בדף זה","אנחנו יוצרים אתכם קשר ושולחים לכם שאלון רקע מקדים שישמש אותנו בהמשך התהליך","#124770"],
                      [QuestionAnswerIcon, "נפגשים ומקשיבים לסיפור" , "ראיונות אישיים המתקיימים בבית, במהלך הראיונות נקשיב לסיפור האישי, לזיכרונות ולקולות שעולים", "#82E6E4"],
                       [CameraAltIcon, "מצטלמים, תחייכו" , "מצלמים את אמא, אבא ואת כל המשפחה המעמד הזה תמיד מאוד מרגש והופך לחוויה חגיגית ומשפחתית", "#FFD459"],
                        [SettingsIcon, "מעבדים את המידע " , "יוצרים מכל המידע שקיבלנו מכם עד כה טקסט סיפורי שייהפוך את הסיפור האישי שלכם למרתק", "#F57854"],
                        [ManageSearchIcon,"משלבים מידע משלים","מאתרים מידע היסטורי ברמה הלאומית והבינלאומית המתממשק עם הסיפורים במסע החיים","#124770"],
                        [AutoFixNormalIcon,"עורכים את התמונות","עורכים את התמונות באיכות גבוהה, מתקנים תאורה וצבעים על מנת קבלת תוצר מקורי אך מיטבי","#82E6E4"],
                         [AutoStoriesIcon,"מייצרים אלבום","מעצבים את כל החומר הכתוב ותמונות מהצילומים באלבום אחד ייחודי המכיל את סיפורכם ושולחים להדפסה","#FFD459"],
                          [CelebrationIcon,"נהנים ביחד","מגיעים לאירוע משפחתי חגיגי, מנחים פעילות חוויתית ומלכדת לכל המשפחה בפעילות בני המשפחה מעלים סיפורים זכרונות וחוויות משותפות שמעניקים רובד נוסף לסיפור החיים","#F57854"]]
                       
                       
        const isDesktop = useMediaQuery({
        query: "(min-device-width: 1200px)",
    });

    
    gsap.registerPlugin(ScrollTrigger)  

    useEffect(() => {
        gsap.to("#timeLine", {
            height: '210vh',
          scrollTrigger: {
            trigger: "#Header",
            start: "top top",
            end: "+=250%",
            scrub: 1
          },

        });
      }, []);

    useEffect(() => {
    gsap.to("#Branch", {
        height: '0.5vh',
        width: '4vw',
        x : '1vw',
        scrollTrigger: {
        trigger: "#Start",
        start: "top top",
        end: "+=2vw",
        scrub: 1
        },

    });
    }, []);
   
    useEffect(() => {
        gsap.to("#Branch2", {
            height: '0.5vh',
            width: '4vw',
            x : '-4vw',
            scrollTrigger: {
            trigger: "#step1",
            start: "top top",
            end: "+=2vw",
            scrub: 1
            },
    
        });
        }, []);
        useEffect(() => {
            gsap.to("#Branch3", {
                height: '0.5vh',
                width: '4vw',
                x : '1vw',
                scrollTrigger: {
                trigger: "#step2",
                start: "top top",
                end: "+=2vw",
                scrub: 1
                },
        
            });
            }, []);
            
            useEffect(() => {
                gsap.to("#Branch4", {
                    height: '0.5vh',
                    width: '4vw',
                    x : '-4vw',
                    scrollTrigger: {
                    trigger: "#step3",
                    start: "top top",
                    end: "+=2vw",
                    scrub: 1
                    },
            
                });
                }, []);
                useEffect(() => {
                    gsap.to("#Branch5", {
                        height: '0.5vh',
                        width: '4vw',
                        x : '1vw',
                        scrollTrigger: {
                        trigger: "#step4",
                        start: "top top",
                        end: "+=2vw",
                        scrub: 1
                        },
                
                    });
                    }, []);
                    useEffect(() => {
                        gsap.to("#Branch6", {
                            height: '0.5vh',
                            width: '4vw',
                            x : '-4vw',
                            scrollTrigger: {
                            trigger: "#step5",
                            start: "top top",
                            end: "+=2vw",
                            scrub: 1
                            },
                    
                        });
                        }, []);
                        useEffect(() => {
                            gsap.to("#Branch7", {
                                height: '0.5vh',
                                width: '4vw',
                                x : '1vw',
                                scrollTrigger: {
                                trigger: "#step6",
                                start: "top top",
                                end: "+=2vw",
                                scrub: 1
                                },
                        
                            });
                            }, []);

                            useEffect(() => {
                                gsap.to("#Branch8", {
                                    height: '0.5vh',
                                    width: '4vw',
                                     x : '-4vw',
                                    scrollTrigger: {
                                    trigger: "#step7",
                                    start: "top top",
                                    end: "+=2vw",
                                    scrub: 1
                                    },
                            
                                });
                                }, []);
       

    return(
        isDesktop?
        <Hero height = "250vh">
            <Grid container>
                <Grid item xs = {12} style = {{marginBottom:'5vh'}} id = "Header">
                    <p className='proccessHeader'>מה כולל תהליך יצירת אלבום סיפור חיים</p>
                </Grid>
                <Grid item xs = {1}/>
                <Grid item xs = {4.5} id = "Start">
                    <Grid container>
                        <Grid item xs = {12} style = {{height:'25vh'}} />               
                        <Grid item xs = {12} id = 'step2'>
                            <ProccessStep  Icon={stepVars[1][0]} header = {stepVars[1][1]} text = {stepVars[1][2]} color = {stepVars[1][3]}></ProccessStep>
                        </Grid>

                        <Grid item xs = {12} style = {{height:'30vh'}}/>

                        <Grid item xs = {12}  id = 'step4'>
                            <ProccessStep  Icon={stepVars[3][0]} header = {stepVars[3][1]} text = {stepVars[3][2]} color = {stepVars[3][3]}></ProccessStep>
                        </Grid>

                        <Grid item xs = {12} style = {{height:'30vh'}}/>

                        <Grid item xs = {12}id = 'step6'>
                            <ProccessStep Icon={stepVars[5][0]} header = {stepVars[5][1]} text = {stepVars[5][2]} color = {stepVars[5][3]}></ProccessStep>
                        </Grid>

                        <Grid item xs = {12} style = {{height:'30vh'}}/>

                        <Grid item xs = {12} id = 'step8'>
                            <ProccessStep Icon={stepVars[7][0]} header = {stepVars[7][1]} text = {stepVars[7][2]} color = {stepVars[7][3]}></ProccessStep>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid container item xs = {1} style = {{justifyContent : 'center'}}>
                    <div className='Timeline'  id="timeLine">
                        <div className ='BranchRight' id="Branch" style = {{marginTop : "10vh" , backgroundColor : "#124770"}}></div>
                        <div className ='BranchLeft' id="Branch2" style = {{marginTop : "25vh" , backgroundColor : "#82E6E4"}}></div>
                        <div className ='BranchRight' id="Branch3" style = {{marginTop : "30vh" , backgroundColor : "#FFD459"}}></div>
                        <div className ='BranchLeft' id="Branch4" style = {{marginTop : "20vh" , backgroundColor : "#F57854"}}></div>
                        <div className ='BranchRight' id="Branch5" style = {{marginTop : "30vh" , backgroundColor : "#124770"}}></div>
                        <div className ='BranchLeft' id="Branch6" style = {{marginTop : "25vh" , backgroundColor : "#82E6E4"}}></div>
                        <div className ='BranchRight' id="Branch7" style = {{marginTop : "25vh" , backgroundColor : "#FFD459"}}></div>
                        <div className ='BranchLeft' id="Branch8" style = {{marginTop : "20vh" , backgroundColor : "#F57854"}}></div>
                    </div>
                </Grid>
                <Grid item xs = {4.5}>
                    <Grid container>
                        <Grid item xs = {12} id = 'step1'>
                            <ProccessStep  Icon={stepVars[0][0]} header = {stepVars[0][1]} text = {stepVars[0][2]} color = {stepVars[0][3]}></ProccessStep>
                        </Grid>
                        <Grid item xs = {12} style = {{height:'30vh'}}/>

                        <Grid item xs = {12} id = 'step3'>
                            <ProccessStep Icon={stepVars[2][0]} header = {stepVars[2][1]} text = {stepVars[2][2]} color = {stepVars[2][3]}></ProccessStep>
                        </Grid>

                        <Grid item xs = {12} style = {{height:'30vh'}}/>

                        <Grid item xs = {12} id = 'step5'>
                            <ProccessStep Icon={stepVars[4][0]} header = {stepVars[4][1]} text = {stepVars[4][2]} color = {stepVars[4][3]}></ProccessStep>
                        </Grid>

                        <Grid item xs = {12} style = {{height:'30vh'}}/>

                        <Grid item xs = {12} id = 'step7'>
                            <ProccessStep Icon={stepVars[6][0]} header = {stepVars[6][1]} text = {stepVars[6][2]} color = {stepVars[6][3]}></ProccessStep>
                        </Grid>               
                    </Grid>
                </Grid>
                <Grid item xs = {1}/>
                <Grid item xs = {4.5}></Grid>
                <Grid item xs = {3} style = {{marginTop:'5vh',marginBottom:'5vh', textAlign:'center'}}>
                    <DetailsButton text = "כן! אשמח לקבל פרטים נוספים"></DetailsButton>
                </Grid>
            

            </Grid>
        
        </Hero>
        :
        <Hero>
        <Grid container>
                <Grid item xs = {11} style = {{marginBottom:'1vh', textAlign : 'right'}}>
                <p className='proccessHeaderMobile'>מה כולל תהליך<br></br> יצירת אלבום סיפור חיים</p>
                </Grid>                         
                {stepVars.map((step, index) => {
                        return(                       
                            <Grid container>
                                <Grid item xs = {1}></Grid>               
                                <Grid item xs = {10}>
                                <ProccessStep Icon={step[0]} header = {step[1]} text = {step[2]} color = {step[3]}></ProccessStep>
                                </Grid>  
                            </Grid>                          
                        )
                    })}    
                <Grid container item xs = {12} style = {{marginTop: '5vh', marginBottom:'5vh', justifyContent:'center'}}>
                    <DetailsButton ></DetailsButton>
                </Grid>              
        </Grid>
       
        </Hero>
    )
}

export default ProccessPage